<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/delivery/add-service')">Add Service</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.id"></el-input>
      </el-form-item>
      <el-form-item label="long_name">
        <el-input placeholder="long_name" v-model="param.longName"></el-input>
      </el-form-item>
      <el-form-item label="home_base">
        <el-input placeholder="home_base" v-model="param.homeBase"></el-input>
      </el-form-item>
      <el-form-item label="manager">
        <el-input placeholder="manager" v-model="param.manager"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="id"
          prop="id"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="longName"
          label="long_name"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="homeBase"
          label="home_base"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="manager"
          label="manager"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
        >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        longName: '',
        homeBase: '',
        manager: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/deliveryServices/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
  .demo-form-inline, .el-pagination {
    margin-top: 10px;
  }
</style>
